import React from "react"
import {useSiteMetadata} from "../hooks/use-site-metadata"

export const SEO = ({title, description, pathname, keywords='', children}) => {
    const {title: defaultTitle, description: defaultDescription, keywords: defaultKeywords, image, siteUrl, author} = useSiteMetadata()

    const seo = {
        title: (title || defaultTitle) + ' | /KALMARS/',
        description: description || defaultDescription,
        keywords: keywords || defaultKeywords,
        image: `${siteUrl}${image}`,
        url: `${siteUrl}${pathname || ``}`,
        author: author || '/KALMARS/',
    }

    return (
        <>
            <title>{seo.title}</title>
            <meta name="title" content={seo.title}/>
            <meta name="description" content={seo.description}/>
            <meta name="keywords" content={seo.keywords}/>
            {seo.image && <meta name="image" content={seo.image}/>}
            <meta name="theme-color" content="#2FC7E5"/>

            <meta property="og:type" content="article"/>
            <meta property="og:title" content={seo.title}/>
            <meta property="og:description" content={seo.description}/>
            {seo.image && <meta property="og:image" content={seo.image}/>}

            <meta name="twitter:card" content="summary_large_image"/>
            <meta name="twitter:title" content={seo.title}/>
            <meta name="twitter:url" content={seo.url}/>
            <meta name="twitter:description" content={seo.description}/>
            <meta name="twitter:creator" content={seo.author}/>
            {seo.image && <meta name="twitter:image" content={seo.image}/>}

            <meta name="robots" content="index, follow"/>
            <meta name="revisit-after" content="14 days"/>
            <meta name="author" content={seo.author}/>

            <link rel="shortcut icon" href="/favicon-32x32.png"/>
            {children}

        </>
    )
}